import React, {
  Fragment,
  useCallback,
  useEffect,
  useState,
} from 'react'

import Box from '../Box'
import Button from '../Button'
import CloseIcon from '../icons/Close'
import Surface from '../Surface'

export interface NoticeProps {
  isHidden?: boolean,
  onDismiss?: () => void
}

const Notice: React.FC<NoticeProps> = ({ children, isHidden, onDismiss }) => {
  const [hidden, setHidden] = useState(isHidden)

  const handleDismissClick = useCallback(() => {
    setHidden(true)
    onDismiss && onDismiss()
  }, [onDismiss, setHidden])

  useEffect(() => {
    setHidden(isHidden)
  }, [
    isHidden,
    setHidden,
  ])

  return (
    <Fragment>
      {!hidden && (
        <Surface>
          <Box
            alignItems="center"
            padding={4}
            row
          >
            {children}
            <Button
              onClick={handleDismissClick}
              round
              size="sm"
              variant="tertiary"
            >
              <CloseIcon />
            </Button>
          </Box>
        </Surface>
      )}
    </Fragment>
  )
}

export default Notice